<script lang="coffeescript">
  import { tick, createEventDispatcher } from 'svelte'
  import { create, all } from 'mathjs'
  math = create(all, { number: 'number'})
  dispatch = createEventDispatcher()

  `export let item`
  `export let index`

  import CommonIcons from "../../../helpers/icons/common.js"

  import InlineInput from "../../../components/common/InlineInput.svelte"
  import IconButton from "../../../components/common/IconButton.svelte"
  import Expression from "./__Expression.svelte"


  result = null

  expressionKeyup = (e)->
    item.expression = e.detail

  expressionChange = (e)->
    dispatch "changeExpression", {id: item.id, expression: e.detail }
    item.expression = e.detail


  deleteLine = (e)->
    dispatch "delete", item.id




  copy = (e)->
    dummy = document.createElement("input")
    document.body.appendChild(dummy)
    dummy.setAttribute("id", "clipboard-#{item.id}")
    document.getElementById("clipboard-#{item.id}").value=evaluateExpression(item.expression)
    dummy.select()
    document.execCommand("copy")
    document.body.removeChild(dummy)





  evaluateExpression = (expression)->
    invalidExpression = ""

    if expression?
      if expression.trim().length > 0
        try
          return math.evaluate(expression)
        catch e
          #console.log e

          try
            dateattempt = Date.create(expression)
            if dateattempt.isValid()
              return dateattempt
            else
              return invalidExpression
          catch e2
            #console.log e2
            return invalidExpression
      else
        return ""
    else
      return ""


</script>
<style lang="sass">

  [layout="row"]
    div:first-child
      padding-left: 0px
      padding-right: 8px
    div
      padding-left: 8px
    div:last-child
      padding: 0px
      min-width: 80px
      .actions
        display: none

    &:hover
      .actions
        display: flex !important


</style>



<section layout="row">
  <div flex="40">
    <InlineInput value={item.expression} on:update={expressionChange} on:keyup={expressionKeyup}/>
  </div>
  <div flex layout="row">
    <div flex>
    <Expression expression={item.expression}/>
    </div>
    <div flex="initial" class="actions">
      <div style="text-align: right; width: 100%">
        <IconButton on:clicked={copy}>{@html CommonIcons.copy}</IconButton>
        <IconButton on:clicked={deleteLine} style="margin-left: 3px">{@html CommonIcons.delete}</IconButton>
      </div>
    </div>
  </div>


</section>
