<script>
  /*
    div
      Listbox
        ListboxField
          input
          svg
          ListboxSelection
          ListboxMenuIcon
        ListboxMenu
          ListboxMenuItem
          ...
  */

  import { createEventDispatcher } from 'svelte';
  const dispatch = createEventDispatcher();

  let className = undefined;
  export { className as class };
  export let disabled = false;

  export let id = Math.random();
  export let invalid = false;
  export let invalidText = '';
  export let items = [];
  export let itemToString = item => item.text || item.id;
  export let light = false;
  export let open = false;
  export let placeholder = '';
  export let selectedIndex = -1;
  export let shouldFilterItem = (i,v) => {
    console.log("shouldFilterItem");
    console.log(i);
    console.log(v);
    true;
  }
  export let size = undefined;
  export let style = undefined;

  export let translateWithId = undefined;
  export let value = "";

  import { afterUpdate } from 'svelte';


  import { cx } from '../lib';
  import ListBox, {
    ListBoxField,
    ListBoxMenu,
    ListBoxMenuIcon,
    ListBoxMenuItem,
    ListBoxSelection
  } from '../ListBox';

  //console.log(`COMBOBOX value ${value}`);

  let selectedId = undefined;
  let inputRef = undefined;
  let inputValue = value;
  console.log("INPUTVALUE #{inputValue}");

  //console.log(`inputvalue = ${value}`);
  let highlightedIndex = -1;

  function change(direction) {
    let index = highlightedIndex + direction;

    if (index < 0) {
      index = items.length - 1;
    } else if (index >= items.length) {
      index = 0;
    }

    highlightedIndex = index;
  }

  afterUpdate(() => {
    if (open) {
      inputRef.focus();
      filteredItems = items.filter(item => shouldFilterItem(item, value));
    } else {
      highlightedIndex = -1;
      //console.log("after update not found?");
      //console.log(`selected item: ${selectedItem}`);
      // inputValue = selectedItem ? selectedItem.text : '';
    }
  });

  let updateSelection = (index) => {
    //
    console.log(index);

    if (index == -1) {
      console.log("cleared");
      dispatch("change",{id: null, text: null});
      inputValue = ""
      value = ""
    } else {
      value = items[selectedIndex].text
      inputValue = items[selectedIndex].text
      console.log("update selection");
      dispatch("change", items[selectedIndex]);
    }
  }
  let keyup = (e)=> {
    console.log("keyup");
    console.log(inputValue);
    console.log(value)
    value = inputValue

  }

  $: ariaLabel = $$props['aria-label'] || 'Choose an item';
  $: menuId = `menu-${id}`;
  $: comboId = `combo-${id}`;
  $: highlightedId = items[highlightedIndex] ? items[highlightedIndex].id : undefined;
  $: filteredItems = items.filter(item => shouldFilterItem(item, value));
  $: selectedItem = items[selectedIndex];
  //$: inputValue = selectedItem ? selectedItem.text : null;
  // $: value = inputValue;
  let hasfocus = false
</script>
<style lang="sass">
  input
    font-size: 14px
    font-weight: 400
    line-height: 14px


    width: 100%
    height: 100% // 2.5rem
    padding: 0px
    color: #161616
    border: none
    transition: background-color 70ms cubic-bezier(0.2, 0, 0.38, 0.9),outline 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
    padding-right: 3rem
</style>
<svelte:body
  on:click={({ target }) => {
    if (open && inputRef && !inputRef.contains(target)) {
      open = false;
    }
  }} />

<div {style}>
  <ListBox
    id={comboId}
    aria-label={ariaLabel}
    {disabled}
    {invalid}
    {invalidText}
    {open}
    {light}
    {size}>
    <ListBoxField
      role="button"
      aria-expanded={open}
      on:click={() => {
        open = true;
      }}
      {id}
      {disabled}
      {translateWithId}>
      <input
        bind:this={inputRef}
        tabindex="0"
        autocomplete="off"
        aria-autocomplete="list"
        aria-expanded={open}
        aria-activedescendant={highlightedId}
        aria-labelledby={comboId}
        aria-disabled={disabled}
        aria-controls={open ? menuId : undefined}
        aria-owns={open ? menuId : undefined}
        class={cx('--text-input', inputValue === '' && '--text-input--empty')}
        on:input={({ target }) => {
          //console.log("on input");
          //console.log(target);
          inputValue = target.value;
        }}
        on:keydown
        on:keydown|stopPropagation={({ key }) => {
          if (key === 'Enter') {
            // todo: take the first available item
            open = !open;
            console.log("enter");
            console.log(filteredItems);
            if (filteredItems.length > 0) {
              value = filteredItems[0].text
              inputValue = filteredItems[0].text
              console.log("update selection");
              dispatch("change", filteredItems[0]);
            }


            //if (highlightedIndex > -1 && highlightedIndex !== selectedIndex) {
            //  selectedIndex = highlightedIndex;
            //  updateSelection(selectedIndex);
            //  open = false;
            // }
          } else if (key === 'Tab') {
            open = false;
          } else if (key === 'ArrowDown') {
            change(1);
          } else if (key === 'ArrowUp') {
            change(-1);
          }
        }}
        on:keyup={keyup}
        on:focus
        on:blur
        on:blur={({ relatedTarget }) => {
          //hasfocus = false
          //console.log("blur")
          //console.log(relatedTarget);
          //if (relatedTarget && relatedTarget.getAttribute('role') !== 'button') {
            //console.log("inputref focus");
            //inputRef.focus();
          //}
        }}
        {disabled}
        {placeholder}
        {id}
        value={inputValue} />
      {#if invalid}
        <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M1 21h22L12 2 1 21zm12-3h-2v-2h2v2zm0-4h-2v-4h2v4z"/></svg>
      {/if}
      {#if (inputValue)}
        <ListBoxSelection
          on:clear={() => {
            selectedIndex = -1;
            updateSelection(selectedIndex);
            open = false;
          }}
          {translateWithId}
          {disabled}
          {open} />
      {/if}
      <ListBoxMenuIcon
        on:click={() => {
          open = !open;
        }}
        {translateWithId}
        {open} />
    </ListBoxField>
    {#if open}
      <ListBoxMenu aria-label={ariaLabel} {id}>
        {#each filteredItems as item, i (item.id || i)}
          <ListBoxMenuItem
            id={item.id}
            active={selectedIndex === i || selectedId === item.id}
            highlighted={highlightedIndex === i || selectedIndex === i}
            on:click={() => {
              selectedId = item.id;
              selectedIndex = items.map(({ id }) => id).indexOf(filteredItems[i].id);
              updateSelection(selectedIndex);
              open = false;
            }}
            on:mouseenter={() => {
              highlightedIndex = i;
            }}>
            {itemToString(item)}
          </ListBoxMenuItem>
        {/each}
      </ListBoxMenu>
    {/if}
  </ListBox>
</div>
