var CodeMirrorModes;

CodeMirrorModes = class CodeMirrorModes {
  // todo: DRY this out
  constructor(subscribe, set, update, constructor_callback) {
    var that;
    this.subscribe = subscribe;
    this.set = set;
    this.update = update;
    this.modes = CodeMirror.modeInfo.sortBy('name');
    this.loadQueue = [];
    this.loadedPaths = [];
    that = this;
    this.exists = function(path) {
      return this.loadedPaths.find(function(item) {
        return item.mode === modeName;
      });
    };
    this.loadlib = function(url, cb) {
      var s;
      if (!document.getElementById(url)) {
        s = document.createElement("script");
        s.setAttribute("type", "text/javascript");
        s.setAttribute("src", url);
        s.setAttribute("id", url);
        s.onload = function(args) {
          console.log(`loaded ${url}`);
          if (cb != null) {
            return cb();
          }
        };
        return document.body.appendChild(s);
      } else {
        return cb();
      }
    };
    this.enqueue = function(items) {
      that = this;
      return items.forEach(function(item) {
        if (!that.exists(item)) {
          //console.log "#{item} added to queue."
          return that.loadQueue.append(item);
        }
      });
    };
    //else
    //  console.log "#{item} already in queue. Skipped."
    this.nextInQueue = function(cb) {
      var currentItem, queueCb;
      that = this;
      if (this.loadQueue.length === 0) {
        //console.log "Queue is empty, nothing to load"
        return cb();
      } else {
        currentItem = this.loadQueue.shift();
        if (this.loadQueue.length === 0) {
          //console.log "#{currentItem} is last item in queue."
          queueCb = cb;
        } else {
          queueCb = function() {
            return that.nextInQueue(cb);
          };
        }
        return that.loadlib(currentItem, queueCb);
      }
    };
    this.modePath = function(name) {
      return `./codemirror/mode/${name}/${name}.js`;
    };
    this.addonPath = function(path) {
      return `./codemirror/addon/${path}.js`;
    };
    this.load = function(modeName, cb) {
      that = this;
      switch (modeName) {
        case "dart":
          this.enqueue([this.modePath("clike")]);
          break;
        case "django":
          this.enqueue([this.modePath("htmlmixed")]);
          break;
        case "dockerfile":
        case "factor":
        case "nsis":
        case "rust":
          this.enqueue([this.addonPath("mode/simple")]);
          break;
        case "haml":
        case "slim":
          this.enqueue([this.modePath("htmlmixed"), this.modePath("ruby")]);
          break;
        case "handlebars":
          this.enqueue([this.addonPath("mode/simple"), this.addonPath("mode/multiplex")]);
          break;
        case "haskell-literate":
          this.enqueue([this.modePath("haskell")]);
          break;
        case "htmlembedded":
          this.enqueue([this.modePath("htmlmixed"), this.addonPath("mode/multiplex")]);
          break;
        case "htmlmixed":
        case "sass":
          this.enqueue([this.modePath("css")]);
          break;
        case "php":
          this.enqueue([this.modePath("htmlmixed"), this.modePath("clike")]);
          break;
        case "pug":
          this.enqueue([this.modePath("css"), this.modePath("htmlmixed")]);
          break;
        case "rst":
          this.enqueue([this.modePath("python")]);
          break;
        case "soy":
        case "tornado":
          this.enqueue([this.modePath("htmlmixed")]);
          break;
        case "twig":
          this.enqueue([this.addonPath("mode/multiplex")]);
      }
      this.enqueue([this.modePath(modeName)]);
      return this.nextInQueue(cb);
    };
    this.hmdLoad = function(mode, scb, ecb) {
      // console.log "hmd mode = #{mode}"
      return that.load(mode, scb);
    };
  }

};

//console.log scb
//console.log ecb
module.exports = CodeMirrorModes;
