var Model, Sugar;

Sugar = require("sugar");

Sugar.extend();

Model = (function() {
  class Model {
    constructor(model) {
      if (model == null) {
        model = this.stub();
      }
      Object.merge(this, Object.select(model, this.attribute_list));
      this.after_construct();
    }

    after_construct() {}

    data_stub() {
      return {};
    }

    stub() {
      return {
        id: null,
        name: "",
        type: this.type,
        data: this.data_stub(),
        is_locked: false,
        is_favorite: false,
        is_expanded: true,
        created_at: null,
        modified_at: null
      };
    }

    validate() {
      return true;
    }

    contains(text) {
      var result;
      result = true;
      if (text != null) {
        result = this.name.toLowerCase().includes(text.toLowerCase());
      }
      return result;
    }

    saveableModel() {
      var model;
      model = Object.select(this, this.attribute_list);
      if (model["id"] != null) {
        return model;
      } else {
        return Object.exclude(model, this.id);
      }
    }

    exportable_clone() {
      var clone, output;
      output = this.saveableModel();
      clone = JSON.stringify(output);
      return JSON.parse(clone);
    }

    safe_clone(classType) {
      var clone, output;
      output = this.saveableModel();
      clone = JSON.stringify(output);
      return new classType(JSON.parse(clone));
    }

  };

  Model.prototype.attribute_list = ["id", "name", "type", "type_version", "data", "is_locked", "is_favorite", "is_expanded", "is_pinned", "priority", "created_at", "modified_at"];

  Model.prototype.editable_attributes = ["name", "data", "is_locked", "is_favorite", "is_archived", "is_pinned", "is_expanded", "priority"];

  Model.prototype.collection_name = "data";

  Model.prototype.type = "";

  return Model;

}).call(this);

module.exports = Model;
