<script lang="coffeescript">

  `export let item`
  import CommonIcons from "../../../helpers/icons/common.js"
  import IconButton from "../../../components/common/IconButton.svelte"
  import uuid from 'uuid'
  import { tick, createEventDispatcher, onMount } from 'svelte'
  import {databaseLoaded,  collection} from "../../../helpers/stores.js"
  import ChecklistItem from "./_ChecklistItem.svelte"
  import DraggableList from "../../../components/common/DraggableList.svelte"




  newItem = null

  saveCb = (r)->
    reset()

  update = ()->
    reindex()
    collection.save {id: item.id, data: { lines: item.data.lines }}, saveCb


  add = ()->
    # set ordinal to max + 1
    newItem.created_at = Date.create()
    newItem.modified_at = newItem.created_at
    item.data.lines.push newItem
    update()


  reset = ()->
    newItem = {
      id: uuid()
      ordinal: item.data.lines.length
      is_complete: false
      description: ""
      due_date: null
      created_at: null
      modified_at: null
      who: null
    }
  reset()



  changeDescription = (e)->
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id


    item.data.lines[itemToChangeIndex].description = e.detail
    update()

  changeIsComplete = (e)->
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id


    item.data.lines[itemToChangeIndex].is_complete = e.detail.is_complete
    update()

  reindex = ()->
    item.data.lines.forEach (item,index)->
      item.ordinal = index

  reorder = (ev)->
    item.data.lines = ev.detail
    update()


  deleteLine = (e)->
    item.data.lines.remove (o)->
      o.id == e.detail
    # todo: reindex ordinals
    update()

  checkKeys = (e)->
    if (e.code == "NumpadEnter") || ((e.code == "Enter") && !e.shiftKey)
      add()

  `$: completed = item.data.lines.count(function(item){ return item.is_complete})`


  mounted = false
  onMount ()->
    mounted = true
</script>
<style lang="sass">

  [container]
    margin: 24px 52px 24px 64px


  header


    margin-bottom: 18px

  input
    width: 100%

  [icon]
    padding-left: 6px
    padding-top: 4px

</style>

<div container>
  <header layout="row">
    <div flex>
    <input type="text" bind:value={newItem.description} on:keyup={checkKeys} placeholder="What needs to be done?"/>
    </div>
    <div flex="initial" icon>
      <IconButton on:clicked={add}>
        {@html CommonIcons.add}
      </IconButton>
    </div>
  </header>
  <section>
    {#if mounted}
    <DraggableList
        list={item.data.lines}
        key="id"
        on:sort={reorder}
        let:item
    		let:index
    >
        <ChecklistItem {item} {index} on:changeDescription={changeDescription} on:changeIsComplete={changeIsComplete} on:delete={deleteLine}/>
    </DraggableList>
    {/if}

  </section>
  <footer>
    {completed} / {item.data.lines.length} completed.
  </footer>
</div>
