<script lang="coffeescript">

  `export let item`

  import uuid from 'uuid'

  import {databaseLoaded,  collection} from "../../../helpers/stores.js"

  import Calendar from "../../../components/braintapper/Calendar/Calendar.svelte"

  import CalendarSettings from "./_CalendarSettings.svelte"

  import Month from "../../../components/braintapper/Calendar/Month.svelte"
  import Week from "../../../components/braintapper/Calendar/Week.svelte"
  import Day from "../../../components/braintapper/Calendar/Day.svelte"
  import List from "../../../components/braintapper/Calendar/List.svelte"



  events = item.data.events
  panel = item.data.last_view


  calendarDate = Date.create()

  saveCb = (r) ->
    console.log 'saved!'
    console.log r
    return

  update = () ->
    collection.save {
      id: item.id
      data:
        last_view: panel
        last_calendar_view_date: Date.create()
        events: events
    }, saveCb
    return

  setPanel = (toPanel)->
    panel = toPanel
    item.data.last_view = toPanel
    update()

  viewChange = (e)->
    console.log "viewchange!"
    panel = e.detail
    update()

  setWeek = (e)->
    console.log "setWeek"
    console.log e.detail
    panel = "week"
    calendarDate = e.detail

  setDay = (e)->
    console.log "setDay"
    panel = "day"
    console.log "calendar view #{panel}"
    console.log e.detail
    calendarDate = e.detail

  setMonth = (e)->
    console.log "setMonth"
    console.log e.detail
    panel = "month"
    calendarDate = e.detail



</script>

<style lang="sass">
  h2
    font-weight: bold
    color: red
    margin: 12px 0px
  container
    height: 300px
    overflow-y: scroll
  [container]
    margin-left: 52px
    margin-right: 52px
</style>
<h2>Timesheets are in active development, you WILL experience bugs</h2>
<div container>
<nav>

  <button on:click={setPanel("month")}>Month</button>
  <button on:click={setPanel("week")}>Week</button>
  <button on:click={setPanel("day")}>Day</button>
  <button on:click={setPanel("list")}>List</button>
  <button on:click={setPanel("settings")}>Settings</button>
Panel: {panel}

<h1>Calendar</h1>
<h2>{calendarDate}</h2>
<h2>{panel}</h2>


</nav>
{#if panel=="list"}
<section>
	<List {events} {calendarDate}/>
</section>
{/if}

{#if panel=="month"}
<Month on:setWeek={setWeek} on:setDay={setDay} on:setMonth={setMonth} {calendarDate} {events}/>
{/if}
{#if panel=="week"}
<Week {calendarDate} on:setDay={setDay} on:setWeek={setWeek} {events}/>
{/if}
{#if panel=="day"}
<Day {calendarDate} on:setDay={setDay} showColumnHeadings={true} showRowHeadings={true} {events}/>
{/if}
{#if panel=="settings"}
<section>
	<CalendarSettings/>
</section>
{/if}
</div>
