var createCodeMirrorInstance, createDbInstance;

import {
  readable,
  writable
} from 'svelte/store';

import Database from "./database.js";

import CodeMirrorModes from "./codemirror_modes.js";

export const databaseLoaded = writable(false);

createDbInstance = function() {
  const { subscribe, set, update } = writable([]);
  var dbcb;
  dbcb = function() {
    return databaseLoaded.set(true);
  };
  return new Database('datastore', subscribe, set, update, dbcb);
};

createCodeMirrorInstance = function() {
  var cb, dbcb;
  cb = function() {
    return console.log("Code mirror modes initialized");
  };
  const { subscribe, set, update } = writable(null);
  dbcb = function() {
    return databaseLoaded.set(true);
  };
  return new CodeMirrorModes(subscribe, set, update, cb);
};

export const collection = createDbInstance();

export const modes = createCodeMirrorInstance();
