<script lang="coffeescript">

  `export let events = []`
  `export let calendarDate`

  import {  createEventDispatcher } from 'svelte'
  import DatePicker from "../DatePicker/DatePicker.svelte"
  dispatch = createEventDispatcher()

  datepickerChange = (e)->
    console.log "datepciker change"
    console.log e.detail
    newEntry.dateString = e.detail.format("{yyyy}-{MM}-{dd}")



  newEntry =
    dateString: Date.create().format("{yyyy}-{MM}-{dd}")
    duration: 420
    start: "09:00"
    end: "16:00"

  `$: calendarDate = Date.create(newEntry.dateString)`

</script>

    Time Entries: {events.length}
    <div>
      <table>
      <thead>
      <tr>
        <td>Date</td>
