<script>
  let className = undefined;
  export { className as class };
  export let id = Math.random();
  export let style = undefined;

  import { cx } from '../lib';

  $: menuId = `menu-${id}`;
</script>
<style lang="sass">
  div[role=listbox-menu]
    border: 1px solid black
    background: white
    position: absolute
    left: 0
    right: 0
    width: 100%
    max-height: 8.75rem
    overflow-y: auto
    z-index: 9100
</style>
<div
  role="listbox-menu"
  id={menuId}
  aria-labelledby={$$props['aria-labelledby']}
  aria-label={$$props['aria-label']}
  class={cx('--list-box__menu', className)}
  {style}>
  <slot />
</div>
