<script lang="coffeescript">

  import { slide } from 'svelte/transition';
  import {databaseLoaded,  collection} from "../../helpers/stores.js"
  import CoreIcons from "../../helpers/icons/core.js"
  import CommonIcons from "../../helpers/icons/common.js"

  `export let item`



  import InlineInput from "../../components/common/InlineInput.svelte"
  import IconButton from '../../components/common/IconButton.svelte'
  import Tooltip from '../../components/carbon/Tooltip/Tooltip.svelte'

  import OverflowMenu from "../../components/carbon/OverflowMenu/OverflowMenu.svelte"
  import OverflowMenuItem from '../../components/carbon/OverflowMenu/OverflowMenuItem.svelte'

  import Note from './items/Note.svelte'
  import Checklist from './items/Checklist.svelte'
  import KeyPair from './items/KeyPair.svelte'
  import Timesheet from './items/Timesheet.svelte'
  import Solver from './items/Solver.svelte'
  import Code from './items/Code.svelte'
  import Markdown from './items/Markdown.svelte'

  import Modal from '../../components/common/ModalGeneric.svelte';
  import ModalFooterDelete from '../../components/common/ModalFooterDelete.svelte';
  import ModalFooterCancel from '../../components/common/ModalFooterCancel.svelte';

  deleteModal = false
  jsonModal = false


  saveCb = (returnedObject)->
    if returnedObject?
      console.log "returnedObject"
      console.log returnedObject
      console.log "saved!"
      Object.merge(item, returnedObject, {deep: true})
      console.log item

    else
      console.log "Save error"



  update = (name)->
    collection.save {id: item.id, name: name}, saveCb

  updateExpanded = ()->
    collection.save {id: item.id, is_expanded: item.is_expanded}, saveCb

  favorite = ()->

    collection.save {id: item.id, is_favorite: item.is_favorite}, saveCb

  lock = ()->
    collection.save {id: item.id, is_locked: item.is_locked}, saveCb


  debouncedUpdate = update.debounce(250)




  deleteItem = ()->
    console.log "Delete confirmed"
    collection.delete item, cancel

  cancel = ()->
    deleteModal = false
    jsonModal = false

  nameChange = (e)->
    update e.detail
    item.name = e.detail

  test = ()->
    console.log item

  menuClose = (e)->

    switch e.detail.text
      when "toggle_favorite"
        item.is_favorite = !item.is_favorite
        favorite()
      when "toggle_lock"
        item.is_locked = !item.is_locked
        lock()
      when "json"
        jsonModal = true
      when "clone"
        cb = (data)->
          return true
        newItem = Object.clone(item, true)
        newItem.name = "#{item.name} clone"
        delete newItem.id

        collection.addStub item.type, newItem, cb
      when "delete"
        deleteModal = true

  toggleSection = ()->
    item.is_expanded = !item.is_expanded
    updateExpanded()
  primaryFocus = true

</script>

<style lang="sass">



  article
    padding: 36px 12px
    &[expanded=false]
      padding: 0px 12px
  footer
    font-size: 10px
    padding: 18px 62px 0px 74px
    div
      display: inline-block
      margin-right: 18px


  .menu
    width: 24px
    &:first-child
      padding-right: 6px
  label
    margin-left: 16px
  textarea[json]
    width: 100%
    height: 100%

  button
    border: none
    background: none
    padding: 6px
    margin: 0px

  header
    margin-bottom: 0px
    margin-right: 60px

  header > div
    width: 100%

  header

    .expander, .item-type
      width: 30px
      padding-right: 6px
      padding-top: 12px

    .menu
      width: 24px
      padding-top: 8px
    .menu, .expander

      button
        padding: 0px

    .title
      padding-left: 6px

    // inline editable css is in the component


</style>


<article expanded={item.is_expanded} transition:slide="{{duration: 100}}">
  <header layout="row" >
    <div flex="initial" class="expander">
      <Tooltip>
        <div slot="trigger">
          <button on:click={toggleSection} open={item.is_expanded}>
            {#if item.is_expanded}
            {@html CommonIcons.expand}
            {:else}
            {@html CommonIcons.collapse}
            {/if}
          </button>
        </div>
        Click to {item.is_expanded? 'collapse': 'expand'}
      </Tooltip>
    </div>
    <div flex="initial" class="item-type">
      {@html CoreIcons[item.type]}
    </div>
    <div flex class="title">
      <h1><InlineInput value={item.name} style="color: #364fc7" on:update={nameChange}/></h1>
    </div>
    <div a="r" class="menu" flex="initial">
      <OverflowMenu on:close={menuClose} flipped={true}>
          <div slot="menu">
            <Tooltip flipped={true}>
              <div slot="trigger">{@html CommonIcons.menu}</div>
              Click here to see more actions.
            </Tooltip>
          </div>


          <OverflowMenuItem text="toggle_favorite" bind:primaryFocus >
            <input type="checkbox" bind:checked={item.is_favorite} hidden>
            {#if item.is_favorite}
              {@html CommonIcons.starred}
            {:else}
              {@html CommonIcons.unstarred}
            {/if}
            <label>
              {#if item.is_favorite}
                Unfavorite
              {:else}
                Favorite
              {/if}
            </label>
          </OverflowMenuItem>
          <OverflowMenuItem text="toggle_lock">
            <input type="checkbox" bind:checked={item.is_locked} hidden>
            {#if item.is_locked}
              {@html CommonIcons.locked}
            {:else}
              {@html CommonIcons.unlocked}
            {/if}
            <label>
              {#if item.is_locked}
                Unlock
              {:else}
                Lock
              {/if}
            </label>
          </OverflowMenuItem>

          <OverflowMenuItem text="clone">
            {@html CommonIcons.clone}
            <label>
              Clone
            </label>
          </OverflowMenuItem>
          <OverflowMenuItem text="json">
            {@html CommonIcons.code}
            <label>
              Copy as JSON
            </label>
          </OverflowMenuItem>
          <OverflowMenuItem text="delete" disabled={item.is_locked}>


              {@html CommonIcons.trash}

              <label>
                Delete
              </label>


          </OverflowMenuItem>
      </OverflowMenu>
    </div>


  </header>
  {#if item.is_expanded}
  <div transition:slide="{{duration: 100}}">
    <section>
        {#if item.type== "note"}
          <Note {item}/>
        {/if}
        {#if item.type== "code"}
          <Code {item}/>
        {/if}
        {#if item.type== "checklist"}
          <Checklist {item}/>
        {/if}
        {#if item.type== "solver"}
          <Solver {item}/>
        {/if}
        {#if item.type== "timesheet"}
          <Timesheet {item}/>
        {/if}
        {#if item.type== "list"}
          <KeyPair {item}/>
        {/if}
        {#if item.type== "markdown"}
          <Markdown {item}/>
        {/if}
    </section>
    <footer>
      <div>Created: {item.created_at.format("")} ({item.created_at.daysAgo()} days ago)</div>
      <div>Modified: {item.modified_at.format("")} ({item.modified_at.daysAgo()} days ago)</div>
    </footer>
  </div>
  {/if}
</article>
{#if deleteModal}
<Modal>
  <h1 slot="header" fg="darkest-red">Delete this item?</h1>
  <p>
  This item will be permanently deleted.
  </p>

  <div slot="footer">
  <ModalFooterDelete on:confirm-delete={deleteItem} on:cancel={cancel}/>
  </div>
</Modal>
{/if}
{#if jsonModal}
<Modal>
  <h1 slot="header">JSON</h1>
  <textarea json>
    {JSON.stringify(item, null, 2)}
  </textarea>

  <div slot="footer">
  <ModalFooterCancel on:cancel={cancel}/>
  </div>
</Modal>
{/if}
