<script>

  import { createEventDispatcher } from 'svelte';
  const dispatch = createEventDispatcher();


  export let autofocus = false;
  export let closeButtonLabelText = 'Clear search input';
  export let id = Math.random();

  export let placeholder = 'Search...';

  export let style = undefined;
  export let type = 'text';
  export let value = '';
  let inputRef = undefined;
</script>
<style lang="sass">
  div

    display: flex
    align-items: center
    position: relative
    width: 100%
    border-radius: 5px

    & > svg:first-child
      left: 0.75rem
      z-index: 2
      position: absolute
      top: 6px
      left: 8px
      fill: #393939
      pointer-events: none
      height: 24px
      width: 24px


    input
      margin: 0px
      font-size: 14px
      line-height: 36px
      letter-spacing: 0
      outline: 2px solid transparent
      outline-offset: -2px
      -webkit-appearance: none
      -moz-appearance: none
      appearance: none
      padding: 0 2.5rem
      text-overflow: ellipsis
      width: 100%
      order: 1
      transition: background-color 110ms cubic-bezier(0.2, 0, 0.38, 0.9),outline 110ms cubic-bezier(0.2, 0, 0.38, 0.9)
      height: 36px
      padding: 0 40px 0 40px

    [show=false]
      display:none

    button
      display: inline-block
      background: none
      appearance: none
      border: 0
      padding: 0
      cursor: pointer
      outline: 2px solid transparent
      outline-offset: -2px
      position: absolute
      right: 0
      display: flex
      align-items: center
      justify-content: center
      transition: opacity 110ms cubic-bezier(0.2, 0, 0.38, 0.9),background-color 110ms cubic-bezier(0.2, 0, 0.38, 0.9),outline 110ms cubic-bezier(0.2, 0, 0.38, 0.9),border 110ms cubic-bezier(0.2, 0, 0.38, 0.9)
      cursor: pointer
      visibility: visible
      opacity: 1
      height: 24px
      width: 24px
      margin-right: 8px
      fill: #161616
      border-style: solid
      border-color: transparent
      border-width: 1px 0

</style>

<div {style}>
  <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/></svg>
  <input
    bind:this={inputRef}
    role="searchbox"
    on:change
    on:keyup
    on:input
    on:input={({ target }) => {
      value = target.value;
    }}
    {autofocus}
    {type}
    {id}
    {value}
    {placeholder} />
  <button
    type="button"
    aria-label={closeButtonLabelText}
    show={(value != '')}
    on:click
    on:click={() => {
      value = '';
      inputRef.focus();
    }}>
    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/></svg>
  </button>
</div>
