<script lang="coffeescript">

  `export let item`


  import uuid from 'uuid'
  import CommonIcons from "../../../helpers/icons/common.js"
  import IconButton from "../../../components/common/IconButton.svelte"
  import { tick, createEventDispatcher, onMount } from 'svelte'
  import {databaseLoaded,  collection} from "../../../helpers/stores.js"
  import Solver from "./_Solver.svelte"
  import Expression from "./__Expression.svelte"
  import DraggableList from "../../../components/common/DraggableList.svelte"

  mounted = false

  onMount ()->
    mounted = true



  expressionElementId = "new-solver-#{item.id}"


  newItem = null

  saveCb = (r)->
    reset(true)

  update = ()->
    reindex()
    collection.save {id: item.id, data: { lines: item.data.lines }}, saveCb


  add = ()->
    # set ordinal to max + 1
    newItem.created_at = Date.create()
    newItem.modified_at = newItem.created_at
    item.data.lines.push newItem
    update()


  reset = (saved) ->
    newItem =
      id: uuid()
      ordinal: item.data.lines.length
      expression: ""
      created_at: null
      modified_at: null


  reset()



  changeExpression = (e)->

    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    item.data.lines[itemToChangeIndex].expression = e.detail.expression
    update()


  placeholder = ()->
    [
      "e.g., 5 feet in centimeters"
      "e.g., 50 * 20 * 1.15"
      "e.g., (365 * 24) + 1"
      "e.g., 12 lbs in kg"
      "e.g., 30 days from now"
      "e.g., Next year"
      "e.g., 80 days ago"
      "e.g., 5 feet in centimeters"
      "e.g., (10.00 + 23.88 + 12.00) * 0.85 * 1.13"

    ].sample()

  reindex = ()->
    item.data.lines.forEach (item,index)->
      item.ordinal = index

  reorder = (ev)->
    item.data.lines = ev.detail
    update()

  deleteLine = (e)->
    item.data.lines.remove (o)->
      o.id == e.detail
    # todo: reindex ordinals
    update()

  checkKeys = (e)->
    if (e.code == "NumpadEnter") || ((e.code == "Enter") && !e.shiftKey)
      if newItem.expression.length > 0
        add()

</script>
<style lang="sass">
  [container]
    margin: 24px 52px 24px 64px


  footer

    div:first-child
      //padding-right: 8px
    div:last-child
      padding-left: 8px

  input
    width: 100%

</style>

<div container>


<section >
    {#if mounted}
    <DraggableList
        list={item.data.lines}
        key="id"
        on:sort={reorder}
        let:item
    		let:index
    >
        <Solver {item} {index} on:changeExpression={changeExpression} on:delete={deleteLine}/>
    </DraggableList>
    {/if}


</section>
<footer layout="row">
  <div flex="40" layout="row">
    <div flex>
    <input id={expressionElementId} type="text" bind:value={newItem.expression} on:keyup={checkKeys} placeholder={placeholder()}/>
    </div>
      <div flex="initial">
          <IconButton on:clicked={add} style="padding-top: 4px">
            {@html CommonIcons.add}
          </IconButton>
      </div>
  </div>

  <div flex="60">
    <Expression expression={newItem.expression}/>
  </div>

</footer>
</div>
