<script lang="coffeescript">
  `export let expression`
  import { create, all } from 'mathjs'
  import IconIndicator from "../../../components/common/IconIndicator.svelte"
  import CommonIcons from "../../../helpers/icons/common.js"

  import Tooltip from '../../../components/carbon/Tooltip/Tooltip.svelte'

  math = create(all, { number: 'number'})


  type = "Math"

  parser = math.parser()


  result = (expr) ->
    output = ""
    try
      output = math.format(parser.evaluate((expr || "").trim()), precision: 14)
      type = math.typeOf(parser.evaluate(expr || "")).titleize()
    catch mathErr
      # console.log "Math error:"
      # console.log mathErr
      try
        dateattempt = Date.create(expr)
        if dateattempt.isValid()
          type = "Date"
          output = dateattempt.format()
        else
          type = "Unknown"
          output = "I don't understand this expression yet."
      catch dateErr
        # console.log "Date error:"
        # console.log dateErr


    finally
      if output == "undefined"
        if expr.length == 0
          output = ""
        else
          output = "I don't understand this expression yet."
        type = "Unknown"
      return (output || "")


  info = () ->
    output = null
    try
      if math.format(parser.evaluate((expression || "").trim())).length - (math.format(parser.evaluate((expression || "").trim()), precision: 14).length) > 4
        output =  "This floating point result contains a round-off error. The raw result is: #{math.format(parser.evaluate((expression || "").trim()))}"
    catch err
      # console.log err
    finally
      return output



  `$: displayResult = result(expression)`



</script>
<style lang="sass">
  div
    height: 100%
    line-height: 35px
  button
    display: inline-block
    padding: 0px
    background: none
    height: 24px
    min-width: 24px
    width: 24px
    border: none

  label
    position: relative
    line-height: 20px
    top: -2px
    font-size: 10px
    color: white
    font-weight: bold
    background: #868e96
    border-radius: 4px
    margin-right: 12px
    display: inline-block
    padding: 2px 8px
    &.Math, &.Number, &.BigNumber
      background: #3b5bdb
    &.Date
      background: #099268
    &.Unknown
      background: #c92a2a
    &.Unit
      background: #e8590c
    &.Complex, &.Fraction, &.Array, &.Matrix
      background: #7048e8
</style>
<div>
  <label class={type}>{type}</label>
  <span>{displayResult}</span>
  {#if info() != null}
    <Tooltip style="display: inline-block; padding-left: 4px; top: 3px">
      <div slot="trigger">{@html CommonIcons.info }</div>
      {info()}
    </Tooltip>
  {/if}
</div>
