<script lang="coffeescript">
  import { tick, createEventDispatcher } from 'svelte'

  dispatch = createEventDispatcher()


  `export let index`
  `export let item`


  import InlineInput from "../../../components/common/InlineInput.svelte"
  import IconButton from "../../../components/common/IconButton.svelte"
  import Checkbox from "../../../components/common/Checkbox.svelte"
  import CommonIcons from "../../../helpers/icons/common.js"

  descriptionChange = (e)->
    dispatch "changeDescription", {id: item.id, description: e.detail }
    item.description = e.detail

  checkboxChange = (e)->
    item.is_complete = e.detail
    dispatch "changeIsComplete", {id: item.id, is_complete: item.is_complete }

  deleteLine = (e)->
    dispatch "delete", item.id

  `$: checked = item.is_complete`
  `$: classes = item.is_complete ? "strikethrough" : ""`

</script>
<style lang="sass">
  [layout="row"]
    padding: 2px 6px
    &:hover
      .actions
        display: flex
  .checkbox
    padding: 6px 12px 0px 0px
  .actions
    padding-left: 6px
    padding-top: 4px
    width: 30px
    display: none

</style>
<div layout="row">
  <div flex="initial" class="checkbox">
    <Checkbox size="24px" bind:checked on:change={checkboxChange}/>
  </div>
  <div flex>
    <InlineInput value={item.description} on:update={descriptionChange} labelClasses={classes}/>
  </div>
  <div class="actions">
    <IconButton on:clicked={deleteLine}>
      {@html CommonIcons.delete}
    </IconButton>
  </div>

</div>
