<script>
  /*
  Expand and collapse icon
  */
  let className = undefined;
  export { className as class };
  export const translationIds = { close: 'close', open: 'open' };
  export let open = false;
  export let style = undefined;
  export let translateWithId = id => defaultTranslations[id];


  import { cx } from '../lib';

  const defaultTranslations = {
    [translationIds.close]: 'Close menu',
    [translationIds.open]: 'Open menu'
  };

  $: description = open ? translateWithId('close') : translateWithId('open');
</script>
<style lang="sass">
  div
    cursor: not-allowed
    right: 0px
    position: absolute
    top: 0
    right: 1rem
    bottom: 0
    -webkit-transition: -webkit-transform 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
    transition: -webkit-transform 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
    transition: transform 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
    transition: transform 70ms cubic-bezier(0.2, 0, 0.38, 0.9), -webkit-transform 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
    cursor: pointer
    > svg

      height: 100%
    &[open=true]
      transform: rotate(180deg)

</style>
<div
  on:click|preventDefault|stopPropagation
  open={open}
  {style}>
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M7.41 8.59L12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z"/></svg>
</div>
