<script lang="coffeescript">

  `export let item`
  import { slide } from 'svelte/transition';
  import uuid from 'uuid'
  import { onMount } from 'svelte'

  element = uuid()


  import Editor from 'cl-editor/dist'

  import {databaseLoaded,  collection} from "../../../helpers/stores.js"

  inlineEdit = null
  inlineEditor = null
  html = item.data.contents

  saveCb = (r)->


  update = (html)->
    console.log "saved"
    collection.save {id: item.id, data: { contents: html}}, saveCb

  debouncedUpdate = update.debounce(500)



  editorIsVisible = false
  handleBlur = (e)->
    if editorIsVisible
      console.log e



  showEditor = (e)->
    console.log "editorIsVisible: #{editorIsVisible}"
    unless editorIsVisible
      editorIsVisible = true
      console.log "showEditor"
      console.log e.target


      inlineEditor = new Editor(
        target: document.getElementById("#{element}-editor")
        data:
          actions: [
            'h1'
            'h2'
            'p'
            'b'
            'i'
            'u'
            'strike',
            'blockquote'
            'ol'
            'ul'
            'a'
            {
               name: 'codeblock'
               icon: '{ }'
               title: 'Code Block'
               result: ()->
                document.execCommand('formatBlock', false, '<PRE>')

            }
            'removeFormat'

          ]
          height: 'auto'
          html: "#{item.data.contents}")

      inlineEditor.on 'blur', ->
        editorIsVisible = false
        item.data.contents = inlineEditor.getHtml()
        update item.data.contents

        inlineEditor.destroy()

      inlineEditor.on 'change', ->
        item.data.contents = inlineEditor.getHtml()
        debouncedUpdate item.data.contents




</script>
<style lang="sass">
  [hidden="true"]
    display: none
  [editor],[preview]
    margin-top: 12px
  [editor]
    margin-left: 52px
    margin-right: 52px
  [preview]
    border-radius: 4px
    width: auto
    min-height: 14px
    margin: 0px 52px 0px 52px
    padding: 24px 24px
    &:hover
      background: #eeeeee
</style>

<div id="{element}" preview on:click={showEditor} hidden={editorIsVisible} class="cl-preview">{@html (item.data.contents || "This note is empty, click to edit")}</div>
<div id="{element}-editor" editor></div>
