<script>
  export let id = undefined;
  export let active = false;
  export let highlighted = false;

  import { cx } from '../lib';
</script>
<style lang="sass">
  div[role=menu-item]
    font-size: .875rem
    font-weight: 400
    line-height: 1.125rem
    letter-spacing: .16px
    height: 2.5rem
    color: #393939
    cursor: pointer
    -webkit-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none
    position: relative
    -webkit-transition: background 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
    transition: background 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
    [role=menu-item-option]
      outline: 2px solid transparent
      outline-offset: -2px
      display: block
      height: 2.5rem
      color: #393939
      text-decoration: none
      font-weight: normal
      line-height: 1rem
      padding: .6875rem 0
      margin: 0 1rem
      border-top: 1px solid transparent
      border-bottom: 1px solid transparent
      border-top-color: #e0e0e0
      text-overflow: ellipsis
      overflow: hidden
      white-space: nowrap
      -webkit-transition: border-color 70ms cubic-bezier(0.2, 0, 0.38, 0.9),color 70ms cubic-bezier(0.2, 0, 0.38, 0.9)
      transition: border-color 70ms cubic-bezier(0.2, 0, 0.38, 0.9),color 70ms cubic-bezier(0.2, 0, 0.38, 0.9)      
</style>
<div
  on:click
  on:mouseenter
  on:mouseleave
  role="menu-item"
  class={cx('--list-box__menu-item', active && '--list-box__menu-item--active', highlighted && '--list-box__menu-item--highlighted')}
  {id}>
  <div role="menu-item-option" class={cx('--list-box__menu-item__option')}>
    <slot />
  </div>
</div>
